<template>
  <div>
    <div class="bgimg" >
      <transition>
        <img :src="images[sliderValue]">
      </transition>
    </div>

    <div class="topList">
      <img src="../assets/witlogo.png" alt="#">
      <h1>|招生信息网</h1>
      <ul>
        <li><a href="#">首页</a></li>
        <li><a href="#">学校概况</a></li>
        <li><a href="#">学院专业</a></li>
        <li><a href="#">招生信息</a></li>
        <li><a href="#">人才培养</a></li>
        <li><a href="#">就业质量</a></li>
        <li><a href="#">特色项目</a></li>
        <li><a href="#">区位优势</a></li>
        <li><a href="#">校友风采</a></li>
      </ul>
    </div>
  </div>

</template>

<script>
export default {
    name:'MyHeader',
    components:{
       
    },
    data() {
    return {
      images: [
         require('../assets/1.png'),
         require('../assets/2.png'),
         require('../assets/3.png'),
         require('../assets/4.jpg'),
         require('../assets/5.png'),
      ],
      sliderValue: 2,
    };
  },
  methods: {
    nextImage(){
      if(this.sliderValue===this.images.length-1){
        this.sliderValue=0
      }
      else{
        this.sliderValue+=1
      }
    },
    lastImage(){
      if(this.sliderValue===0){
        this.sliderValue=this.images.length-1
      }
      else{
        this.sliderValue-=1
      }
    }
  }, 
  mounted(){
    setInterval(
      this.nextImage,5000
    )
  }    
}

</script>

<style scoped>
ul{
    list-style: none;
}
.bgimg{
    position: relative;
    width: 100%;
}
.bgimg img{
    width: 100%;
    height: 500px;
    object-fit: cover;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
.topList{
    float: left;
    position: absolute;
    top: 10px;
    width: 100%;
}
.topList img{
    float: left;
    margin-left: 50px;
    margin-top: 20px;
    color: white;
}
.topList h1{
    height: 30px;
